<template>
	<div id="pageBox" ref="pageBox">
		<div v-if="!pageLoading">
			<Header></Header>
			<Banner :banner="allData.slider"></Banner>
			<Classification :classArr="classArr"></Classification>
			<div class="choice pd14">
				<div class="title">精选</div>
				<div class="content">
					<div class="leftImg">
						<img src="" alt="">
					</div>
					<div class="rightImg">
						<div class="topImg">
							<img src="" alt="">
						</div>
						<div class="bottomImg">
							<img src="" alt="">
						</div>
					</div>
				</div>
			</div>

			<div class="mostTab pd14">
				<ul class="tabBox">
					<li class="tabItem" :class="{active:nowTab==1}">
						<span @click="mostTabChange(1)">最新</span>
					</li>
					<li class="tabItem" :class="{active:nowTab==2}">
						<span @click="mostTabChange(2)">最热</span>
					</li>
				</ul>
				<span class="tabHk" :class="'tabHk'+nowTab"></span>
			</div>
			<div class="pd14">
				<CommonList :listData="listData"></CommonList>
			</div>
		</div>
		<!-- <img class="loading" v-if="pageLoading" src="@/assets/img/loading.gif"> -->
		<div class="gotoTop" @click="gotoTopFun" :class="{toTopShow:toTopShow,toTopHidden:!toTopShow}">
			<i class="icon-totop iconfont importantColor"></i>
		</div>
	</div>
</template>

<script type="text/javascript">
	import { mapState, mapMutations, mapActions } from 'vuex'

	import Header from "@/components/header"
	import Banner from "@/components/banner"
	import SongSheet from "@/components/songSheet"
	import Classification from "@/components/classification"
	import CommonList from "@/components/commonList"

	export default {
		components:{
			Banner,
			Header,
			SongSheet,
			Classification,
			CommonList
		},
		data(){
			return {
				pageLoading:true,
				allData:{},
				listData:[
					{imgUrl:"",title:"这是一个测试测试测试测试测试测试测试测试测试测试测试标题",note:"最近更新9期",pNum:"1.9万人气",money:"¥999"},
					{imgUrl:"",title:"这是一个测试测试测试测试测试测试测试测试测试测试测试标题",note:"最近更新9期",pNum:"1.9万人气",money:"¥999"},
					{imgUrl:"",title:"这是一个测试测试测试测试测试测试测试测试测试测试测试标题",note:"最近更新9期",pNum:"1.9万人气",money:"¥999"},
					{imgUrl:"",title:"这是一个测试测试测试测试测试测试测试测试测试测试测试标题",note:"最近更新9期",pNum:"1.9万人气",money:"¥999"},
					{imgUrl:"",title:"这是一个测试测试测试测试测试测试测试测试测试测试测试标题",note:"最近更新9期",pNum:"1.9万人气",money:"¥999"},
					{imgUrl:"",title:"这是一个测试测试测试测试测试测试测试测试测试测试测试标题",note:"最近更新9期",pNum:"1.9万人气",money:"¥999"},
					{imgUrl:"",title:"这是一个测试测试测试测试测试测试测试测试测试测试测试标题",note:"最近更新9期",pNum:"1.9万人气",money:"¥999"}
				],
				nowTab:1,
				toTopShow:false
			}
		},
		methods:{
			getDataFun(){
				var time = new Date().getTime();
				var url = "https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg?g_tk=5381&uin=0&format=jsonp&jsonpCallback=jsonp1&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&_="+time;
				this.pageLoading = true;
				this.$jsonP(url,"jsonp1",(res)=>{
					this.pageLoading = false;
					this.allData = res.data;
				})
			},
			mostTabChange(tabNum){
				this.nowTab = tabNum;
			},
			// 回到顶部
			gotoTopFun(){
				this.animationToTop(300);
			},
			animationToTop(time){
				let intervalTime = 10;
				let scrollY = window.scrollY;
				let once = (intervalTime / time * scrollY).toFixed(0);
				let timer = setInterval(()=>{
					scrollY = scrollY - once;
					if(scrollY <= 0){
						scrollY = 0;
						clearInterval(timer);
					}
					scrollTo(0,scrollY);

				},intervalTime)
			},
			windowScroll(){
				window.onscroll = () => {
	        let winScrollY = window.scrollY;
	        if(300 <= winScrollY){
	          this.toTopShow = true;
	        }else{
	          this.toTopShow = false;
	        }
	      }
			}
		},
		computed:{
			...mapState({
				classArr:state => state.CommonStore.classArr
			})
		},
		created(){
			this.getDataFun();
			this.windowScroll();
		}
	}
</script>

<style lang="less" scoped>
	#banner{
		margin-bottom:14px;
	}
	#pageBox{
		.choice{
			width:100%;
			margin-top:10px;
			.title{
				font-size:0.16rem;
				font-weight: bold;
				margin-bottom:10px;
			}
			.content{
				width:100%;
				overflow:hidden;
				.leftImg{
					float:left;
					width:1.68rem;
					height:2.4rem;
					background:#999;
				}
				.rightImg{
					float:right;
					width:1.68rem;
					height:2.4rem;
					.topImg{
						width:1.68rem;
						height:1.15rem;
						background:#999;
					}
					.bottomImg{
						margin-top:0.1rem;
						width:1.68rem;
						height:1.15rem;
						background:#999;
					}
				}
			}
		}
		.mostTab{
			position:relative;
			width:100%;
			margin-top:20px;
			margin-bottom:20px;
			line-height: 0.3rem;
			.tabBox{
				width:100%;
				border-bottom:1px solid #dedede;
				.tabItem{
					display: inline-block;
					width:0.46rem;
					font-size:0.16rem;
					color:#666;
					&.active{
						color:#31c27c;
						font-weight: bold;
					}
				}
			}
			.tabHk{
				position:absolute;
				background:#31c27c;
				width:0.34rem;
				height:4px;
				bottom:0;
				transition:all ease-out 100ms;
				&.tabHk1{
					left:0.14rem;
				}
				&.tabHk2{
					left:0.63rem;
				}
			}
		}
		.gotoTop{
			position:fixed;
			width:0.5rem;
			height:0.5rem;
			background:#fff;
			border-radius: 50%;
			box-shadow: 0 0 10px 1px #dedede;
			right:0.2rem;
			text-align: center;
			line-height: 0.5rem;
			z-index: 98;
			transition: all linear 600ms;
			i{
				font-size:0.24rem;
			}
			&.toTopShow{
				bottom:1rem;
			}
			&.toTopHidden{
				bottom:-1.5rem;;
			}
		}
	}
</style>



